.scroll-btn {
  position: fixed;
  right: 30px;
  bottom: 30px;
  background-color: $dark-blue;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
  display: none;
  justify-content: center;
  align-items: center;
  img {
    fill: $white;
  }
  &:active {
    background: $gradient-bg;
  }
}
